@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
.header-container{background-color:#ffe600;max-height:56px;height:56px}.header-container__logo{display:flex;justify-content:flex-end;align-self:center;grid-column:2 / 3}.header-container__search{display:flex;align-self:center;justify-content:flex-start;width:100%}.header-container__search form{position:relative;width:100%}.header-container__search__input{border-radius:2px;border:0 rgba(0,0,0,0.2);padding:8px 0px 8px 8px;max-width:95%;width:100%;font-size:18px !important}.header-container__search__btn{position:absolute;border-radius:0 2px 2px 0;border:0 rgba(0,0,0,0.2);background-color:#eee;max-width:5%;width:100%;border-left:1px solid #eee}.header-container__search__btn__icon{font-size:16px;line-height:24px}@media (max-width: 480px), (max-width: 600px){.header-container__search__input{max-width:100%}.header-container__search__btn{display:none}}@media (max-width: 600px), (max-width: 768px){.header-container__search__input{max-width:90%}.header-container__search__btn{max-width:10%}}

.footer-container{max-height:56px;height:56px}

.card-container{display:grid;grid-template-columns:196px 2fr 1fr;grid-template-rows:minmax(196px, 196px);grid-template-areas:"photo description location";padding:20px 0;background-color:#fff;border-radius:4px;cursor:pointer}.card-container__image{display:flex;justify-content:center;align-items:center;grid-area:photo}.card-container__image img{max-width:180px;max-height:180px;border-radius:4px;width:auto;height:auto}.card-container__description{display:flex;justify-content:flex-start;flex-flow:column;padding:0px 8px 0px 8px;grid-area:description}.card-container__description__price{display:inline;font-size:24px}.card-container__description__price img{padding-left:8px}.card-container__description__text{font-size:18px;color:#333}.card-container__location{display:flex;justify-content:flex-start;padding:32px 8px 32px 8px;font-size:12px;color:#666;grid-area:location}@media screen and (max-width: 768px){.card-container{grid-template-areas:"photo description description" "photo location location"}}@media screen and (max-width: 480px){.card-container{grid-template-areas:"photo photo photo" "description description description" "location location location"}.card-container__description{justify-content:center;flex-flow:wrap}.card-container__location{display:none}}

.products-container__list{grid-column:2 / 4}

.products-container__detail{grid-column:2 / 4}.product-container{display:grid;grid-template-columns:2fr 1fr;grid-gap:0.5rem;grid-template-areas:"photo description" "detail description"}.product-container__image{display:flex;flex-direction:column;align-items:center;grid-area:photo}.product-container__image__img{max-width:680px;max-height:680px;border-radius:4px;width:100%;height:auto}.product-container__description{display:flex;justify-content:flex-start;flex-flow:column;padding:0px 8px 0px 8px;grid-area:description}.product-container__description__h1{font-size:46px;color:#000;font-weight:bolder}.product-container__description__h2{font-size:24px;color:#000;font-weight:bolder}.product-container__description__p{font-size:14px;color:#333}.product-container__description__btn{border-radius:4px;border:1px solid;background-color:#3483fa;color:#fff;height:auto;padding:0px 16px 0px 16px;padding-top:12px;padding-bottom:12px;margin-right:16px}.product-container__detail{grid-area:detail}.product-container__detail__h2{align-self:flex-start;color:#333;font-size:24px;font-weight:bolder}.product-container__detail__p{align-self:flex-start;font-size:16px;color:#999}@media screen and (max-width: 768px){.product-container{grid-template-areas:"photo photo" "description description" "detail detail"}}

.breadcrumb-container{max-height:56px;height:56px;font-size:14px !important}.breadcrumb-container__ol{margin:0;display:flex;padding:0;flex-wrap:wrap;list-style:none;align-items:center;grid-column:2 / 4}.breadcrumb-container__ol__li a{text-decoration:none;color:#666}.breadcrumb-container__ol__li a:hover{text-decoration:underline}.breadcrumb-container__ol__delimiter{display:flex;margin-left:8px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin-right:8px}

body{font-family:'Nunito', sans-serif;color:#333;margin:0px;background-color:#eee}@media (min-width: 992px){body{padding-top:0}}h1,h2,h3,h4,h5,h6{font-family:'Nunito', sans-serif;font-weight:700;text-transform:uppercase;color:#000}input{font-family:'Nunito', sans-serif}input[type="text"]{background-color:#fff;color:#333;font-size:16px;outline:0}button{font-family:'Nunito', sans-serif;cursor:pointer;background-image:none;right:0;bottom:0;padding:0;height:100%}.grid-container{display:grid;grid-template-columns:1fr 56px 5fr 1fr;grid-column-gap:0.5rem}

